@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
  font-display: swap;
}
body {
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  min-width: 320px;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}
.full-width-in-limited {
  width: 100vw;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}
.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
}
@media (min-width: 960px) {
  .container {
    max-width: 960px;
  }
}
.title {
  font-size: 24px;
  font-weight: 300;
  line-height: 33px;
  text-align: center;
  color: #283645;
}
@media (min-width: 660px) {
  .title {
    font-size: 40px;
    font-weight: 300;
    line-height: 1.2;
  }
}
@media (min-width: 960px) {
  .title {
    font-size: 50px;
    font-weight: 300;
    line-height: 68px;
  }
}
.button {
  font-family: inherit;
  font-size: 18px;
  font-weight: 700;
  line-height: 30px;
  background-color: #2AAB6D;
  border: none;
  border-radius: 1px;
  color: #FFFFFF;
  padding: 23px 30px;
  text-align: center;
  text-transform: uppercase;
}
.button:hover,
.button:focus {
  background-color: #1C9B5E;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #E5E5E5;
}
.page-header {
  position: relative;
}
.page-header__wrap {
  background-color: #283645;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.page-header__container {
  display: grid;
  grid-template-rows: auto auto;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 17px;
  padding-top: 23px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 0;
}
.page-header__logo {
  grid-column: 1/1;
}
.page-header__toggle {
  display: none;
  font-family: inherit;
  justify-self: flex-end;
  margin-top: -5px;
  grid-column: 2/-1;
  width: 50px;
  height: 30px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  padding-bottom: 0;
  background-color: transparent;
  border: none;
  position: relative;
  cursor: pointer;
}
.page-header__toggle::before,
.page-header__toggle::after {
  content: "";
  width: 30px;
  height: 2px;
  position: absolute;
  background-color: #FFFFFF;
  left: 24px;
  transform: rotate(45deg);
  top: 15px;
}
.page-header__toggle::before {
  transform: rotate(-45deg);
}
.page-header__toggle:hover::before,
.page-header__toggle:hover::after {
  background-color: #D22856;
}
.page-header__toggle:active::before,
.page-header__toggle:active::after {
  background-color: rgba(210, 40, 86, 0.3);
}
.page-header__toggle.support-js {
  display: inline-block;
}
.page-header__toggle--hamburger::before {
  transform: rotate(0deg);
  top: 3px;
  height: 3px;
  width: 48px;
  left: 0;
}
.page-header__toggle--hamburger::after {
  transform: rotate(0deg);
  height: 3px;
  width: 48px;
  left: 0;
  top: 13px;
  box-shadow: 0 9px 0 0 #FFFFFF;
}
.page-header__toggle--hamburger:hover::after {
  box-shadow: 0 9px 0 0 #D22856;
}
.page-header__toggle--hamburger:active::after {
  box-shadow: 0 9px 0 0 rgba(210, 40, 86, 0.3);
}
.page-header--closed .page-header__wrap {
  background-color: rgba(0, 0, 0, 0.3);
}
.page-header--closed .page-header__nav {
  display: none;
}
.page-header--closed .page-header__bottom {
  position: static;
}
.page-header__nav {
  grid-column: 1 / -1;
  margin-left: -20px;
  margin-right: -20px;
  margin-top: 3px;
}
.page-header__bottom {
  position: relative;
}
@media (min-width: 660px) {
  .page-header__container {
    padding-top: 26px;
    grid-row-gap: 40px;
  }
  .page-header__toggle {
    margin-top: 14px;
  }
  .page-header__nav {
    margin-top: 0;
  }
}
@media (min-width: 960px) {
  .page-header__wrap {
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__bottom {
    position: static;
    padding-top: 100px;
    padding-bottom: 85px;
  }
  .page-header__container {
    max-width: 960px;
    margin-left: auto;
    margin-right: auto;
    grid-template-columns: 200px 1fr;
    grid-row-gap: 17px;
    padding-left: 10px;
    padding-right: 10px;
    padding-bottom: 0;
  }
  .page-header__logo {
    margin-left: 0;
  }
  .page-header__toggle,
  .page-header__toggle.support-js {
    display: none;
  }
  .page-header--closed .page-header__nav {
    display: block;
  }
  .page-header__nav {
    grid-column: 2/-1;
    margin-top: 5px;
    margin-left: 0;
    margin-right: 0;
  }
}
.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav-list__link {
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  padding: 15px 20px 16px 20px;
  display: block;
  box-sizing: border-box;
  border-top: 1px solid #000000;
  box-shadow: inset 0 1px rgba(255, 255, 255, 0.15);
}
.nav-list__link:hover,
.nav-list__link:focus {
  color: #D22856;
}
.nav-list__link:active {
  opacity: 0.3;
  color: #D22856;
}
.nav-list__item:last-child .nav-list__link {
  border-bottom: 1px solid #000000;
}
@media (min-width: 960px) {
  .nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    box-shadow: none;
  }
  .nav-list__link {
    font-size: 18px;
    font-weight: 400;
    line-height: 18px;
    text-transform: none;
    border: none;
    box-shadow: none;
    padding: 15px 0;
    margin-right: 80px;
    margin-bottom: 10px;
  }
  .nav-list__link:active {
    color: #FFFFFF;
  }
  .nav-list__link--active {
    box-shadow: 0 2px 0 0 #FFFFFF;
  }
  .nav-list__item:last-child .nav-list__link {
    border: none;
    margin-right: 0;
  }
}
.get-app {
  display: grid;
  padding-top: 25px;
  grid-template-columns: 1fr repeat(2, minmax(auto, calc(960px / 2))) 1fr;
  grid-template-rows: max-content minmax(285px, auto) max-content;
}
.get-app::before {
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("../img/look-life-differently-m.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top center;
  background-color: #283645;
  z-index: -1;
  position: absolute;
  top: 0;
}
@media (min-resolution: 2dppx) {
  .get-app::before {
    background-image: url("../img/look-life-differently-m@2x.jpg");
  }
}
.get-app__title {
  grid-column: 2/4;
  grid-row: 1/2;
  color: #FFFFFF;
  display: block;
  line-height: 28.8px;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 27px;
}
.get-app__download {
  position: relative;
  grid-column: 2/4;
  grid-row: 5/6;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 20px;
}
.get-app__download::before {
  content: "";
  width: 320px;
  height: 286px;
  background-image: url("../img/pink-app-example-m.png");
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}
@media (min-resolution: 2dppx) {
  .get-app__download::before {
    background-image: url("../img/pink-app-example-m@2x.png");
  }
}
@media (min-width: 660px) {
  .get-app {
    grid-template-columns: 1fr repeat(2, minmax(310px, calc(960px / 2))) 1fr;
    grid-template-rows: auto;
    padding-bottom: 74px;
    padding-top: 95px;
  }
  .get-app::before {
    background-image: url("../img/look-life-differently-t.jpg");
  }
  .get-app__title {
    font-size: 40px;
    font-weight: 300;
    line-height: 48px;
    grid-column: 2/3;
    padding-right: 10px;
    margin-bottom: 0;
  }
  .get-app__download {
    grid-column: 2/3;
    position: static;
  }
  .get-app__download::before {
    background-image: url("../img/pink-app-example-t.png");
    width: 660px;
    height: 545px;
    bottom: 0;
    z-index: -1;
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .get-app::before {
    background-image: url("../img/look-life-differently-t@2x.jpg");
  }
}
@media (min-width: 660px) and (min-resolution: 2dppx) {
  .get-app__download::before {
    background-image: url("../img/pink-app-example-t@2x.png");
  }
}
@media (min-width: 960px) {
  .get-app {
    padding-top: 100px;
    padding-bottom: 85px;
  }
  .get-app::before {
    background-image: url("../img/look-life-differently-d.jpg");
  }
  .get-app__title {
    font-size: 50px;
    font-weight: 300;
    line-height: 68px;
    margin-right: 170px;
  }
  .get-app__download {
    margin-right: 170px;
    padding-top: 40px;
  }
  .get-app__download::before {
    background-image: url("../img/pink-app-example-d.png");
    width: 911px;
    height: 648px;
    right: 25px;
  }
}
@media (min-width: 960px) and (min-resolution: 2dppx) {
  .get-app::before {
    background-image: url("../img/look-life-differently-d@2x.jpg");
  }
}
@media (min-width: 960px) and (min-resolution: 2dppx) {
  .get-app__download::before {
    background-image: url("../img/pink-app-example-d@2x.png");
  }
}
