.how-work {
  display: grid;
  grid-template-columns: 1fr minmax(320px, 960px) 1fr;
  padding-top: 15px;
}
.how-work__title {
  font-size: 24px;
  font-weight: 300;
  line-height: 29px;
  grid-column: 2/3;
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 30px;
}
.how-work__sentence-separator {
  margin-bottom: 30px;
  content: "";
  display: block;
}
.how-work__wrap {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: 1fr minmax(280px, 960px) 1fr;
  background-color: #F2F2F2;
  padding-top: 65px;
  position: relative;
}
.how-work__wrap::before {
  content: "";
  position: absolute;
  top: 0;
  border-top: 32px solid #FFFFFF;
  border-right: 100vw solid #F2F2F2;
}
.how-work__list {
  grid-column: 2/3;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-right: 20px;
  margin-left: 20px;
}
.how-work__item {
  margin-bottom: 28px;
  text-align: center;
}
.how-work__image {
  margin-bottom: -3px;
  margin-top: 26px;
}
.how-work__item:last-child {
  margin-bottom: 0;
}
.how-work__item:not(:last-child) .how-work__image {
  display: none;
}
.how-work__subtitle {
  font-size: 18px;
  font-weight: 700;
  line-height: 24px;
  color: #000000;
  text-align: center;
  margin-bottom: 10px;
}
.how-work__text {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
}
@media (min-width: 660px) {
  .how-work {
    padding-top: 7px;
  }
  .how-work__wrap::before {
    content: "";
    border-top-width: 52px;
  }
  .how-work__title {
    font-size: 40px;
    font-weight: 300;
    line-height: 54px;
  }
  .how-work__sentence-separator {
    margin-bottom: 0;
  }
  .how-work__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    position: relative;
    padding-bottom: 28px;
  }
  .how-work__item {
    grid-column: 2/2;
    margin-bottom: 8px;
  }
  .how-work__subtitle {
    text-align: left;
    margin-top: 10px;
  }
  .how-work__text {
    text-align: left;
  }
